/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"page-classes\">\n   <div class=\"breadcrum position-relative float-left w-100 mb-5\">\n      <h3 class=\"title-txt text-center w-100 float-left text-white\">Between us portal</h3>\n   </div>\n   <div class=\"container\">\n      <div class=\"row mb-5\">\n       <div class=\"col-lg-12 col-md-12\">\n         <div class=\"about-desc\">\n            <p>\n                <b>Dear Parents,</b><br>\t\t\t\n                Between us portal is a Parent  School communication website as well as mobile app.\n            </p>\n            <div class=\"abt-title mt-5\">\n               <h3 class=\"table-title-txt mb-3\">The Between Us Portal will help to</h3>\n            </div>\n            <ul class=\"with-icon\">\n                <li>Update the parent information / Emergency Information as well as address / Contact details.</li>\n                <li>View the academic resources.</li>\n                <li>Receive circulars and announcement.</li>\n                <li>Communicate with the school / teachers via message option.</li>\n                <li>Get attendance information.</li>\n            </ul>\n            <div class=\"abt-title mt-5\">\n               <h3 class=\"table-title-txt mb-3\">How to login as a Parent</h3>\n            </div>\n            <ul class=\"with-icon\">\n                <li>On admission, a username and one time password for using the portal send to all the parents on their registered mobile number and / or on their email id.</li>\n                <li>Go to the website  <a href=\"https://www.betweenus.in\"> www.betweenus.in</a></li>\n                <li>Login using the username and one time password given on admission.</li>\n                <li>Click on the relevant option on the home screen.</li>\n            </ul>\n            <div class=\"abt-title mt-5\">\n               <h3 class=\"table-title-txt mb-3\">Troubleshooting</h3>\n            </div>\n            <p>1. If you have forgotten the password please follow the procedure given below.</p>\n            <ul class=\"with-icon pl-5\">\n                <li>Complete the online form on<a href=\"https://www.betweenus.in/Forgetpass.aspx\"> www.betweenus.in/Forgetpass.aspx</a></li>\n                <li>On filling the form , you will receive an email as well as SMS with the new password.</li>\n            </ul>\n            <p>2. In case of any queries , please write to it. Support@ppls.org with the following information.</p>\n            <ul class=\"with-icon pl-5\">\n                <li>School name</li>\n                <li>Between Us Username</li>\n                <li>Concern/ Issue</li>\n                <li>Contact name / Mobile no</li>\n            </ul>\n         </div>\n       </div>\n       <div class=\"clearfix\"></div>\n       <div class=\"col-lg-12 col-md-12 mt-5\">\n          <div class=\"abt-title\">\n             <h3 class=\"table-title-txt mb-3\">Admission - rules/guidelines</h3>\n          </div>\n          <div class=\"about-desc\">\n           <ul class=\"with-icon\">\n               <li> Certain documents like Birth Certificate, Transfer Certificate,Mark Sheet of Previous Class should be submitted to the Fee counter for maintenance of record. These documents should be submitted within 15 days from the date of admission failing which the provisional admission stands cancelled.<b></b></li>\n               <li> In class-I the Date of Birth certificate has validity in place of T.C. Certificates from Civic Bodies, Govt. Hospitals, Reputed Private Hospitals will be treated as genuine proof of D.O.B. only for Class I.<b></b></li>\n               <li>Till T.C. and all other related documents are not submitted, the admission is only provisional and can stand cancelled.<b></b></li>\n               <li>No demand for changing the Date of Birth will be entertained. (Unless, T.C. counter signed by the DIOS with changed records from the previous school is submitted in the T.C. office).<b></b></li>\n               <li>As per School norms students will not be allowed to change their Class once the registration has been done, unless they take permission from the Principal.<b></b></li>\n           </ul>\n          </div>\n       </div>\n       <div class=\"clearfix\"></div>\n       <div class=\"col-lg-12 col-md-12 mt-5\">\n          <div class=\"abt-title\">\n             <h3 class=\"table-title-txt mb-3\">withdrawal - rules/guidelines</h3>\n          </div>\n          <div class=\"about-desc\">\n           <ul class=\"with-icon\">\n               <li> For T.C. the applicant should first clear all dues (fee, library, N.C.C., Scouts &amp; Guides, Sports, Board Exam etc.</li>\n               <li>Counter signed T.C will be issued after 6 days from the date of submission of application.</li>\n               <li>T.C. without countersignature will be issued after 3 days from the date of submission of the application.</li>\n               <li>A duplicate or triplicate copy of T.C. will be issued only after an affidavit and newspaper cutting is submitted informing us that the TC is lost.</li>\n               <li>No T.C. will be issued after the issue of the triplicate T.C.</li>\n               <li>In case of new admission in the mid-session, a child has to pay fees for 6 months as well as for the transport facility also the rule will be the same.</li>\n           </ul>\n          </div>\n       </div>\n       <div class=\"clearfix\"></div>\n       <div class=\"col-lg-12 col-md-12 mt-5\">\n          <div class=\"abt-title\">\n             <h3 class=\"table-title-txt\">Safety rules</h3>\n          </div>\n          <div class=\"about-desc\">\n             <h6 class=\"mt-2\">Rules for Younger Children</h6>\n             <ul class=\"with-icon\">\n                 <li><span>I KNOW my name , address telephone number , and my parents names.</span></li>\n                 <li><span>I always CHECK FIRST with my parents or the person in charge. I tell them before I go anywhere or get into a car , even with someone I know.</span></li>\n                 <li><span>I always CHECK FIRST with my parents or a trusted adult before I accept anything from anyone, even from someone  I know.</span></li>\n                 <li><span>\tI always TAKE A FRIEND with me when I go to places or play outside.</span></li>\n                 <li><span>\tI SAY NO if someone tries to touch me or treat me in a way that makes me feet , scared, uncomfortable, or confused.</span></li>\n                 <li><span>\tIts ok to SAY NO and I know that there will always be someone who can help me.</span></li>\n                 <li><span>I Know that I can TELL my parents or a trusted adult if I feel scared, uncomfortable , or confused.</span></li>\n                 <li><span>\tI am STRONG , SMART , and have the right to be safe.</span></li>\n             </ul>\n             <h6 class=\"mt-5\">Rules for Older Children </h6>\n             <ul class=\"with-icon\">\n                 <li>DONT GO OUT ALONE. There is safety in numbers. This rule isnt just for little kids, it applies to teens ,too.</li>\n                 <li>ALWAYS TELL AN ADULT WHERE YOURE GOING. Letting someone know where youll be at all times is smart. if youre faced with a risky situation or get into trouble, your family and friends will know where to find you.</li>\n                 <li>SAY NO IF YOU FEEL THREATEDND. If someone- anyone touches you in a way that makes you feel uncomfortable, you have the right to say no. if you are pressurised in doing something that you know is wrong, be strong and stand your ground.</li>\n             </ul>\n          </div>\n       </div>\n       <div class=\"clearfix\"></div>\n      </div>\n   </div>\n</div>");

