/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"student-vision\">\n   <div class=\"breadcrum position-relative float-left w-100 mb-5\">\n      <h3 class=\"title-txt text-center w-100 float-left text-white\">Chairman's Desk</h3>\n   </div>\n   <div class=\"container mb-5\">\n      <div class=\"row\">\n         <div class=\"col-12 text-center mb-3 logo-wrapper\">\n            <img src=\"./assets/images/desk/chairmans-desk.jpg\" style=\"width:300px\" class=\"mb-3\" alt=\"Chairman's Desk\">\n            <h4>Dr. Dinesh Pratapwar</h4>\n         </div>\n         <div class=\"col-12 text-center\">\n            <div class=\"about-desc\">\n               <p class=\"mb-4\"> I am pleased that you are considering <span class=\"font-weight-bold\">The SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR</span>  Among your school choices. This is an important and exciting time in your life. Are you ready to be inspired? Are you willing to work hard and to grow? Are you eager and uninhibited to question and find joy as you learn? If so, then <span class=\"font-weight-bold\">the SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR</span> is the place to be.</p>\n               <p class=\"mb-4\">The curriculum is ever evolving and has a progressive outlook with an intellectual rigor, where a sound foundation of critical thinking and articulating is laid in an environment that encourages curiosity and discovery and all this as you have fun.</p>\n               <p class=\"mb-4\">The extra-curricular activities are varied, enriching and exciting, where you will learn new skills and make life-long friends and associations. You will see yourself increasingly getting involved in visual and performing arts, in debates and sports; as you get enthused you will realize your full potential and learn to appreciate teamwork.</p>\n               <p class=\"mb-4\">In senior classes, you'll have opportunities to take on leadership roles, where you'll learn to accept and handle responsibility, manage and advise others and make fair and effective decisions. You'll help conceive and plan events, assist in labs and libraries, represent your peers; in effect you shall, from a child move towards being a young responsible adult, be a vanguard, an exemplar.</p>\n               <p class=\"mb-4\">The SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR  teacher shall be the epitome of all that is good; who is passionate, caring and non-partisan as they seek out the very best within every child.</p>\n               <p class=\"mb-4\">The SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR  parent shall be an important part of our community. Their involvement, help and support in enriching lives at homes and on campus will be immeasurable. They will be involved with projects or volunteer their time and effort in any other manner.</p>\n               <p class=\"mb-4\">It would be my pleasure to welcome you and your family to the SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR  community. If questions arise as you go through this prospectus, please call or e-mail us.</p>\n               <p class=\"text-right mb-0 \">Best wishes,</p>\n               <p class=\"text-right mb-0\">Dr. Dinesh Pratapwar</p>\n               <p class=\"text-right font-weight-bold\">SAPTAGIRI PODAR LEARN SCHOOL DEGLOOR</p>\n            </div>\n         </div>\n      </div>\n   </div>\n</div>");

